// eslint-disable-next-line @typescript-eslint/no-unused-vars
import { MENU_BUTTON } from "../constants.mjs";

export enum ButtonOptEnum {
  FONT_SIZE = `${MENU_BUTTON}--font-size`,
  LETTER_SPACING = `${MENU_BUTTON}--letter-spacing`,
  LEGIBLE_FONT = `${MENU_BUTTON}--legible-font`,
  HIGHLIGHT_LINKS = `${MENU_BUTTON}--highlight-links`,
  CONTRAST = `${MENU_BUTTON}--contrast`,
  LARGE_POINTER = `${MENU_BUTTON}--large-pointer`,
  RESET = `${MENU_BUTTON}--reset`,
}

export interface ButtonOpt {
  /**
   * With this we can reference the language map {@link LANG_MAP} and assign attributes.
   */
  type: ButtonOptEnum;
  /**
   * Some opts have a range of values, e.g. font size.
   * If a button has only a true false value, then we can assign a value of 1.
   * @value number is 0 no bars will be displayed
   * @default 1
   */
  levels: number;

  /**
   * Font awesome icon name.
   */
  icon?: string;
  clickHandlers?: Array<(e: Event) => void>;
}